.container {
  @include vp-1200 {
    width: 1160px;
    max-width: 1160px;
    margin: 0 auto;
    position: relative;

  }

  @include vp-1199 {
    width: 678px;
    max-width: 678px;
    margin: 0 auto;
    position: relative;
  }

  @include vp-767 {
    width: 290px;
    max-width: 290px;
    margin: 0 auto;
    position: relative;

    &--hero {
      width: 320px;
      max-width: 320px;
      margin: 0;
    }
  }
}
